@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Roboto Mono", monospace;
}

.landing {
  height: 100vh;
  background-image: url(/my-music-journey/static/media/Landing.1c2a1740.jpg);

  display: flex;
  align-items: center;
  justify-content: center;
}

.nav {
  position: absolute;
}

.container {
  padding: 20px;
  border: 8px solid white;
  width: 45%;
  padding: 120px;
  color: white;
  position: absolute;
  font-size: 35px;
  text-align: center;
}

.item {
  width: 200px;
  height: 200px;
  margin: 50px auto;
  padding-top: 75px;
  background: #ccc;
  text-align: center;
  color: #fff;
  font-size: 3em;
}

* {
    text-decoration: none;
}

.nav {
    position: absolute;
    width: 100%;
    top: 5%;

    display: flex;
    justify-content: center;
}

.nav a {
    color: white;
    font-size: 25px;
    padding-bottom: 10px;
    transition-duration: 0.2s;
}

.nav a:hover {
    border-bottom: 6px solid white;
}

.nav-text {
    display: flex;
    justify-content: space-between;
    width: 300px;
}
* {
  text-decoration: none;
  color: white;
}

.view {
  background-color: #16182b;
  width: 100%;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}

.view-container {
  width: 90%;
  height: 80vh;
  background-color: #16182b;
  border: 8px solid white;
}

.view-container img {
  margin: 30px 0 0 30px;
  width: 50px;
  position: relative;
}

.view-content {
  float: left;
  width: 80%;
  height: 100%;
  padding: 10% 0 0 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.view-content-header {
  height: 9%;
  position: absolute;
}

.view-video {
  width: 500px;
  height: 300px;
}

.scroll-menu {
  height: 100%;
  width: 20%;
  float: right;
  overflow-y: auto;
  overflow-x: hidden;
  border-left: 5px solid white;
}

.music-title {
  padding: 20px 10px 20px 10px;
  color: white;
  border-bottom: 5px solid white;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #86879c;
}

::-webkit-scrollbar-thumb {
  background: #353963;
  width: 10px;
  border-radius: 30px;
}

* {
  text-decoration: none;
  color: white;
  box-sizing: border-box;
  font-family: "Roboto Mono", monospace;
}

.upload {
  background-color: #16182b;
  width: 100%;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}
.upload-container {
  width: 90%;
  height: 80vh;
  background-color: #16182b;
  border: 8px solid white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upload-container img {
  margin: 30px 0 30px 30px;
  width: 50px;
  position: relative;
}

.upload-container-header {
  width: 100%;
}

.drop-zone {
  width: 400px;
  height: 400px;
  padding: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: "Quicksand", sans-serif;
  font-weight: 500;
  font-size: 20px;
  cursor: pointer;
  color: white;
  border: 4px dashed white;
  border-radius: 10px;
}

.drop-zone--over {
  border-style: solid;
  color: white;
}

.drop-zone__input {
  display: none;
}

.drop-zone__thumb {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
  background-size: cover;
  position: relative;
}

.drop-zone__thumb::after {
  content: attr(data-label);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  color: white;
  background: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  text-align: center;
}

.upload-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 800px;
}

.upload-form-content {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.upload-form-input {
  display: flex;
  flex-direction: column;
}

.upload-form input[type="text"] {
  background-color: transparent;
  border: 2px solid white;
  padding: 20px;
  border-radius: 5px;
  width: 350px;
  height: 25px;
  margin-bottom: 30px;
}

.upload-form input:focus::-webkit-input-placeholder {
  color: transparent;
}

.upload-form input:focus:-ms-input-placeholder {
  color: transparent;
}

.upload-form input:focus::-ms-input-placeholder {
  color: transparent;
}

.upload-form input:focus::placeholder {
  color: transparent;
}

.upload-form textarea:focus::-webkit-input-placeholder {
  color: transparent;
}

.upload-form textarea:focus:-ms-input-placeholder {
  color: transparent;
}

.upload-form textarea:focus::-ms-input-placeholder {
  color: transparent;
}

.upload-form textarea:focus::placeholder {
  color: transparent;
}

.upload-form textarea {
  background-color: transparent;
  border: 2px solid white;
  padding: 20px;
  border-radius: 5px;
  width: 350px;
  height: 150px;
}

.upload-form input::-webkit-input-placeholder, .upload-form textarea::-webkit-input-placeholder {
  color: white;
}

.upload-form input:-ms-input-placeholder, .upload-form textarea:-ms-input-placeholder {
  color: white;
}

.upload-form input::-ms-input-placeholder, .upload-form textarea::-ms-input-placeholder {
  color: white;
}

.upload-form input::placeholder,
.upload-form textarea::placeholder {
  color: white;
}

.input-submit {
  font-size: 20px;
  margin-top: 80px;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 5px;
  width: 150px;
  height: 50px;
}

.uploadProgress {
  margin-top: 20px;
  width: 250px;
  height: 30px;
}

